header {
  background: #EFEFEF;
  padding: 2.5px;
  border-bottom: 1px solid #999;
  position:fixed;
  width: 100vw;
  border-radius: 0 0 70% 70%;
  z-index: 10;
  opacity: 0.9;
}

header .identity {
  font-size: 2.7em;
  margin:0 auto;
  text-align: center;
}

.identity {
  font-family: 'QuickSand';
  color: #005f8c;
}



