nav {
  font-size: 1em;
  width: 100%;
  float: left;
  position: fixed;
  top:65px;
  left:0;
  display: none;
}

nav ul {
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

nav ul li:hover {
  background: red;
}

nav ul li {
  padding: 0.5em;
}

nav ul li a {
  -webkit-transform: skew(15deg);
      -ms-transform: skew(15deg);
          transform: skew(15deg);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}



